<template>
  <div id="app">
    <header><component :is="header" :header_name="header_name"></component></header>
    <div id="app-scroller">
      <!-- <div id="pages"> -->
        <div id="fullpage" class="page sections" :class="classes[key]" v-for="(page, key) in pages" :key="key" :data-state="key">
          <div class="section" 
            v-for="(section, key2) in page" 
            :key="section.post_name" 
            :id="section.post_name" 
            :data-permalink="section.permalink"
            :data-index="key2"
            :data-title="section.post_title"
            :style="section.style" >
              <component   
                :section="section" 
                :is="Templates[section.template]"
                :post="section" 
                :posts="section.children" 
                :tag="wp.wp_query.queried_object"
                :cat="wp.wp_query.queried_object"
                :author="wp.wp_query.queried_object"
                :s="wp.wp_query.query_vars.s"
                @template_mounted="template_mounted" 
                class="section-wrap" ></component>
          </div>
        </div>
      <!-- </div> -->
      <!-- Footer -->
      <footer><component :is="footer" :footer_name="footer_name"/></footer>
      <!-- <AudioPlayerBottom />
      <Editor v-if="wp.user_can.edit_theme_options" /> -->
    </div>
    <div id="page-loader">
      <v-icon name="spinner" class="fa-pulse"></v-icon>
    </div>
    <div class="owy-mailto-ico hide"><v-icon name="at" class="at"/></div>
    <!-- <GpdrIntro /> -->
  </div>
</template>

<script>

//Header
//import Header from 'Organisms/header.vue'
// import Header from 'Organisms/simple-header.vue'
//import Header from 'Organisms/header-dropdown.vue'


//Footer
//import Footer from 'Organisms/footer-santos-palace.vue'
//import GpdrIntro from 'Molecules/gpdr-intro.vue'

//import AudioPlayerBottom from 'Organisms/audio-player-bottom.vue'
//import Editor from 'Organisms/editor.vue'

//Functions
//import is from 'is_js'
import init_scrolltop from 'Libs/init-scrolltop.js'
//import scrollSection from 'Libs/scroll-sections.js'
// import scrollSection from 'Libs/scroll-sections-v2.js'
import links_and_anchors from 'Libs/links-and-anchors.js'
import get_new_page from 'Libs/get-new-page.js'
import animate_next_page from 'Libs/animate-next-page.js'
//import smart_fonts from 'Libs/smart-fonts.js'
import on_screen from 'Libs/on-screen.js'
import woocommerceAjax from 'Libs/woocommerce-ajax.js'
import acf_ajax from 'Libs/acf-front-ajax.js'


function vue_key_to_name(str)
{
  var base = new String(str).substring(str.lastIndexOf('/') + 1); 
  if(base.lastIndexOf(".") != -1) base = base.substring(0, base.lastIndexOf("."));
  return base;
}

export default {
  name: 'App',
  data(){
    return {
      Templates: {},
      pages:{
        'current':{},
        'next':{}
      },
      classes:{
        'current': '',
        'next': '',
      },
      header : null,
      header_name : null,
      footer: null,
      footer_name: null
    }
  },
  components: {
    //Header,
    //Footer,
    //GpdrIntro
    //AudioPlayerBottom,
    //Editor
  },
  created (){
    //preload blocks
    const blocks_config = require.context('Blocks/', true, /\.(config\.js)$/i);
    blocks_config.keys().map(key => {

      let file = key.substring(2)   
      let block_object = require( `Blocks/${file}` ).default
      
      this.$store.state.blocks[block_object.slug] = block_object
    })

    //automatically load templates
    const req = require.context('Templates/', true, /\.vue$/i);
    req.keys().map(key => {

      let name = vue_key_to_name( key )
      let file = key.substring(2)
      
      this.Templates[name] = () => import( `Templates/${file}` )

    });

    //load builder default vue for grapes templates
    require.context('Organisms/', true, /builder-html\.vue$/i);
    let builder_html = () => import( `Organisms/builder-html.vue` )

    this.jquery.each(this.$store.state.wp.builder_templates_list, (index) => {
      this.Templates[index] = builder_html
    });

    console.log('Templates', this.Templates);

  },
  mounted (){

    console.log( 'App mounted', this.$store.state.wp );
    
    this.pages['current'] = this.wp.sections
    this.classes['current'] = this.wp.body_class

    this.$store.commit({
        type: 'sections_load',
        sections: this.pages['current'],
      })

    //navigate in the website
    this.$(document).ready( ($) => {
      //console.log('JQuery Ready', $, this.ajaxurl)
      
      //$.fn.notify = notify
      links_and_anchors(this)

      woocommerceAjax(this)
      acf_ajax(this)
      
      //console.log('store', this.$store.state.wp)
      //init scroll if child
      init_scrolltop(this)

      //auto scroll next section
      // scrollSection(this)

      //ready
      $(document).trigger('first_page_ready')
      
      $(document).on('reload_query', (event, wp) => {
        this.classes['current'] = ''
        this.pages['current'] = wp.sections

        this.classes['current'] = wp.body_class
        this.pages['current'] = wp.sections
        this.$store.commit({
          type: 'sections_load',
          sections: this.pages['current'],
        })
      });

      $(document).on('new_page', (event) => { /* event from: links-and-anchors.js */
        
        console.log('new-page');
        
        $('#app').data('scrolling', 'new-page')

        //$('#footer').hide();
        
        get_new_page( this, event.href, (wp) => {
          
          //console.log( 'app get page', wp )
          let new_sections = wp.sections
          //this.wp = wp
          //

          $(document).trigger('after_data_next_page')

          //console.log( 'app get page this', this.wp )
          this.pages['next'] = new_sections
          this.classes['next'] = wp.body_class
          
          setTimeout( ()=>{

            $(document).trigger('before_next_page')

            animate_next_page( this, event.href, () => {

              this.classes['current'] = ''
              this.pages['current'] = ''

              
              this.pages['next'] = {}
              this.classes['next'] = '' 
              this.$store.state.wp = wp

              this.classes['current'] = wp.body_class
              this.pages['current'] = new_sections

              this.$store.commit({
                type: 'sections_load',
                sections: this.pages['current'],
              })
              
              setTimeout( ()=>{

                //$('#footer').show();

                init_scrolltop(this)
                
                links_and_anchors(this)

                setTimeout( ()=>{

                  $('#app').data('scrolling', '')

                  //links_and_anchors(this)

                  $(document).trigger('after_next_page')
                }, 1 )
              }, 1 )
            })
          }, 1 )
        })
      });

      on_screen()

    });

  },
  methods : {
    template_mounted(){

      //let $ = this.$

      //console.log('template_mounted')

      /*if( is.object(comp) )
      {
        
        let section = this.pages['current'][$(comp.$el).parent('.section').data('index')]

        acf_to_css( comp.$el, {
          'background-color' : 'background->color',
          ':hover' : {
            'background-color' : 'background->color'
          }
        }, section)
      }*/

      //smart_fonts()
    }
  },
  computed: {
    wp () {   
      //console.log('wp update',this.$store.state.wp);
      window.wp = this.$store.state.wp
      return this.$store.state.wp
    }
  },
  watch: {
    '$store.state.wp.current_section' : function(){
      let template = this.$store.state.wp.current_section.template
      let builder_templates_list = this.$store.state.wp.builder_templates_list

      console.log('test', this.Templates)

      if( builder_templates_list[template] )
      {
        template = builder_templates_list[template]
        if( template['metas']['header'] )
        {
          this.header = this.Templates[ template['metas']['header'] ]
          this.header_name = template['metas']['header']
        }
        if( template['metas']['footer'] )
        {
          this.footer = this.Templates[ template['metas']['footer'] ]
          this.footer_name = template['metas']['footer']
        }
      }
    }
  }
}
</script>

<style>

/*@import '~Styles/wp-content.css';
@import '~Styles/noty.css';
@import '~Styles/woocommerce.css';
@import '~Styles/acf-front.css';
@import '~Styles/santospalace.css';*/


/* width */
::-webkit-scrollbar { 
  width: 5px;
  height: 5px;
}

/* Track */
#app::-webkit-scrollbar-track {
  background: rgba(33,33,33,1);
}
#app *::-webkit-scrollbar-track {
  background: rgba(255,255,255,0.05);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #595959; 
  /*border-radius: 7px;*/
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #595959;
}

html{
  height: 100vh;
  width: 100vw;
  overflow:hidden;
}
body{
  padding: 0;
  margin: 0;
}

* {
  box-sizing: border-box;
}

#app {
  position: absolute;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  z-index: 0;
}

#app-scroller {
  z-index: 0;
}

.clear{
  display: inline-block;
}



#header{
  z-index: 3000;
  position: fixed;
  width: 100%;
}

/*#pages{
  min-height: 100vh;
}*/

.page[data-state="next"]{
  z-index: 1000;
}

#page-loader{
  position: fixed;
  height: 100vh;
  width: 100%;
  top: 0;
  background: rgba(0,0,0,0.5);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 8000;
}

#page-loader .fa-icon{
  height: 5vh;
  width: 5vh;
  color: rgba(255,255,255,0.5);
}

.hide{
  display: none;
}
</style>